import {
  Card,
  Breadcrumb,
  Form,
  Button,
  Radio,
  Input,
  Upload,
  Space,
  Select,
  message
} from 'antd'
import { PlusOutlined } from '@ant-design/icons'
import { Link, useNavigate, useSearchParams } from 'react-router-dom'
import { observer } from 'mobx-react-lite'
import './index.scss'
import ReactQuill from 'react-quill'
import 'react-quill/dist/quill.snow.css'
import { useStore } from '@/store'
import { useState, useRef, useEffect } from 'react'
import { http } from '@/utils'
const { Option } = Select
const Publish = () => {
  const { channelStore } = useStore()

  // 存放上傳圖片的列表
  const [fileList, setFileList] = useState([])
  // 使用 useRef 聲明一個暫存倉庫
  const cacheImgList = useRef([])
  const onUploadChange = ({ fileList }) => {
    // 採取受控的寫法：在最後一次 log 裡 response
    // 最終 react state fileList中存放的數據有 response.data.url
    // 此為關鍵位置：需要做數據格式化
    const formatList = fileList.map(file => {
      // 上傳完畢後，做數據處理
      if (file.response) {
        return {
          url: file.response.data.url
        }
      }
      // 否則在上傳中，則不進行任何處理
      return file
    })
    setFileList(formatList)
    // 同時把圖片列表存入一份到倉庫裡
    cacheImgList.current = formatList
  }

  // 切換圖片
  const [imgCount, setImageCount] = useState(1)
  const radioChange = (e) => {
    console.log(e.target.value)
    // 此判斷是依據採取到的原始值，不透過 useState 方法修改之後的數據
    // useState 修改之後的數據，無法同步獲取修改之後的新值
    const rawValue = e.target.value
    setImageCount(rawValue)
    // 從倉庫裡面取對應的圖片數量，交給用來渲染圖片列表的 fileList
    // 透過調用 setFileList 的方法
    // 無圖模式
    if (rawValue === 0) {
      return false
    }
    // 單圖模式
    if (rawValue === 1) {
      const img = cacheImgList.current[0]
      setFileList(!img ? [] : [img])
    }
    else if (rawValue === 3) {
      setFileList(cacheImgList.current)
    }
  }

  // 提交表單
  const navigate = useNavigate()
  const onFinish = async (values) => {
    console.log(values)

    // 數據二次處理，重點是處理 cover 字段
    const { channel_id, content, title, type } = values
    const params = {
      channel_id,
      content,
      title,
      type,
      cover: {
        type: type,
        images: fileList.map(item => item.url) // 問題點
      }
    }
    console.log(params)
    if (id) {
      await http.put(`/mp/articles/${id}?draft=false`, params)
    }
    else {
      await http.post('/mp/articles?draft=false', params)
    }

    // 跳轉列表並提示用戶
    navigate('/article')
    message.success(`${id ? '更新成功' : '發布成功'}`)
  }

  // 編輯表單
  // 文案適配 路由參數 id 判斷條件
  const [params] = useSearchParams()
  const id = params.get('id')
  console.log('route:', id)

  // 數據回填 id 調用接口 1.表單回填 2.暫存列表 3.Upload 組件 fileList
  const [form] = Form.useForm()
  useEffect(() => {
    const loadDetail = async () => {
      const res = await http.get(`/mp/articles/${id}`)
      // 表單數據回填 實例方法
      const data = res.data
      // 表單數據回填
      form.setFieldsValue({ ...data, type: data.cover.type })
      // 調用 setFileList 方法回填 upload
      const formatImgList = data.cover.images.map(url => ({ url }))
      setFileList(formatImgList)
      console.log(res)
      // 暫存列表裡也存一份(暫存列表和 fileList 回顯列表保持數據結構統一即可)
      cacheImgList.current = formatImgList
      // 图片type
      setImageCount(data.cover.type)
    }
    // 必須是編輯狀態，才可以發送請求
    if (id) {
      loadDetail()
      console.log(form.current)
    }
  }, [id, form])

  return (
    <div className="publish">
      <Card
        title={
          <Breadcrumb separator=">">
            <Breadcrumb.Item>
              <Link to="/">首頁</Link>
            </Breadcrumb.Item>
            <Breadcrumb.Item>{id ? '編輯' : '發布'}文章</Breadcrumb.Item>
          </Breadcrumb>
        }
      >
        <Form
          labelCol={{ span: 4 }}
          wrapperCol={{ span: 16 }}
          initialValues={{ type: 1 }}
          onFinish={onFinish}
          form={form}
        >
          <Form.Item
            label="標題"
            name="title"
            rules={[{ required: true, message: '請輸入文章標題' }]}
          >
            <Input placeholder="請輸入文章標題" style={{ width: 400 }} />
          </Form.Item>
          <Form.Item
            label="頻道"
            name="channel_id"
            rules={[{ required: true, message: '請選擇文章頻道' }]}
          >
            <Select placeholder="請選擇文章頻道" style={{ width: 400 }}>
              {channelStore.channelList.map(item => (
                <Option key={item.id} value={item.id}>{item.name}</Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item label="封面">
            <Form.Item name="type">
              <Radio.Group onChange={radioChange}>
                <Radio value={1}>單圖</Radio>
                <Radio value={3}>三圖</Radio>
                <Radio value={0}>無圖</Radio>
              </Radio.Group>
            </Form.Item>
            {imgCount > 0 && (
              <Upload
                name="image"
                listType="picture-card"
                className="avatar-uploader"
                showUploadList
                action="http://geek.itheima.net/v1_0/upload"
                fileList={fileList}
                onChange={onUploadChange}
                multiple={imgCount > 1}
                maxCount={imgCount}
              >
                <div style={{ marginTop: 8 }}>
                  <PlusOutlined />
                </div>
              </Upload>
            )}
          </Form.Item>
          {/* 這裡的富文本組件，已經被 Form.Item 控制 */}
          {/* 它的輸入內容會在 onFinished 回調中收集起來 */}
          <Form.Item
            label="內容"
            name="content"
            rules={[{ required: true, message: '請輸入文章內容' }]}
          >
            <ReactQuill
              className="publish-quill"
              theme="snow"
              placeholder="請輸入文章內容"
            />
          </Form.Item>

          <Form.Item wrapperCol={{ offset: 4 }}>
            <Space>
              <Button size="large" type="primary" htmlType="submit">
                {id ? '更新' : '發布'}文章
              </Button>
            </Space>
          </Form.Item>
        </Form>
      </Card>
    </div>
  )
}

export default observer(Publish)